e. After spending months traveling with the blind monk, Udyr had learned to decipher the\n                meaning behind Ionian speech.")]),_c('p',[_vm._v("Reaching the precipitous eastern slopes of Winterspike, Udyr paused to gaze out at the\n                lake before him, frozen in all its majesty. At the edges of the lake laid the bones and corpses of\n                wild beasts, as well as those of dead shamans and priests who had come to this place, months, years,\n                and lifetimes before him.")]),_c('p',[_vm._v("Udyr stood still, his chest bare, eyes closed, bracing against the brisk morning air.\n            ")]),_vm._m(7),_c('p',[_vm._v("He looked down at his reflection on the ice. It showed the face of a man, ragged and\n                worn from his travels. ")]),_vm._m(8),_c('p',[_vm._v("The ice stirred. A crack at first, as Udyr saw his image splinter into disparate\n                pieces. Soon, entire slabs broke off, drifting apart. Udyr waited, respectfully. ")]),_c('p',[_vm._v("The frigid water bubbled. Slowly to begin with, and then rapidly all at once. Steam\n                rose from the surface, filling the air with heat.")]),_c('p',[_vm._v("Udyr took in a quiet breath, his shoulders rising, to ready himself.")]),_c('p',[_vm._v("Out of the mist leapt an ice-formed beast, carved by the land’s magic and birthed from\n                the lake. The ground trembled as it took a thundering step toward Udyr. ")]),_c('p',[_vm._v("Udyr looked up at the wild spirit towering above him, three times his height.")]),_c('p',[_vm._v("The murmurs started low, soft—leaves falling on fresh snow. Yet quickly, they grew.\n            ")]),_c('p',[_vm._v("Bitter. Restless. ")]),_vm._m(9),_c('p',[_vm._v("Grunts turned into snarls, mutters into barks, one swallowing another. Their rage\n                seized his mind, shattering his every thought. At first, the voices competed for dominance—elnüks,\n                drüvasks, and others. Udyr had heard these voices within himself many times. Soon, they joined\n                together, into the shape he had most feared. ")]),_c('p',[_vm._v("The ravenous tiger.")]),_vm._m(10),_c('p',[_vm._v("Udyr only just mustered the strength to stifle a gasp. His knees buckled under the\n                weight of the noise in his head. His hands shot toward the soil to steady his body. Straining his\n                neck, he glared up at the savage creature, not willing to answer.")]),_c('p',[_vm._v("The voices rose at the sight of Udyr’s inaction, with the tiger roaring above the\n                rest. ")]),_vm._m(11),_c('p',[_vm._v("Udyr braced himself as the spirit rammed its head into him, shards of its icy body\n                cutting into his skin. Tumbling far across the ground, Udyr landed against hard rock.")]),_vm._m(12),_c('p',[_vm._v("Regaining his balance, he wiped the blood off his face and forced his hands into\n                fists. He punched his knuckles into the frosty ground, and the throbbing in his arms overtook him.\n                Veins pulsated from his hands to his shoulders. Getting back to his feet, Udyr readied himself to\n                deflect another blow.")]),_vm._m(13),_c('p',[_vm._v("The spirit charged headfirst. Udyr tried dodging out of the way, but his foe was\n                quicker and stronger. As he rolled to the side, the tiger swiped his leg with its claws, spraying\n                the spirit walker’s blood across the frosted ground. ")]),_c('p',[_vm._v("Udyr knelt on one knee in pain. He felt his own anger building, but still he held\n                back.")]),_vm._m(14),_c('p',[_vm._v("The spirit loomed closer, letting out a feral cry before pouncing toward Udyr.\n                Realizing he could not dodge in time, he crossed his arms before him and clenched his fists. Magical\n                energy surrounded him, blocking the tiger spirit’s lethal blow. ")]),_c('p',[_vm._v("The spirit slid backward. After regaining its footing, it grinned through its fangs.\n                Its icy body crackled with vicious energy, splintering the bones of its past victims beneath its\n                feet. Death was all this place knew. ")]),_c('p',[_vm._v("Udyr knelt on both knees now, his head down, his body pounding with pain while the\n                spirit paced around him. He felt the ground shake with its every step.")]),_vm._m(15),_c('p',[_vm._v("He gritted his teeth, their points drawing blood from his lips as he felt the ground\n                tremble once more. ")]),_vm._m(16),_c('p',[_vm._v("Udyr looked up, seeing the spirit charge toward him, its eyes lusting for blood—so\n                wide he could see himself staring back, with the same lust for violence.")]),_vm._m(17),_c('p',[_vm._v("Golden flames erupted from Udyr’s skin like wildfire, the anger coursing through his\n                body matching the fury of the tiger spirit before him.")]),_vm._m(18),_c('p',[_vm._v("Udyr roared as he rushed straight at the tiger spirit. Vaulting onto the beast’s leg,\n                he climbed its craggy surface, smashing his bleeding hands into whatever piece of ice he could to\n                pull himself upward. The creature shook, the sharp edges of its body piercing the spirit walker’s\n                skin. Udyr screamed, relishing in his might. At last, his wrath matched the fire in his foe, as the\n                two reveled in their savage violence.")]),_c('p',[_vm._v("With a brutal lunge, he reached the spirit’s back, trails of his blood dripping down\n                its sides. Spirit energy surged through him, a force strong enough to drown out any pain. The voices\n                of wild beasts clamored unchecked in his mind—the bitter cries of those consumed by the tiger, and\n                his own unbridled anger—merging as one. ")]),_c('p',[_vm._v("“I am no prey!”")]),_c('p',[_vm._v("Udyr brought his fists down in a flurry of explosive blows, creating a web of cracks\n 